.songbook {
  touch-action: pan-y;
  padding-bottom: 60px;
}

.songtitle {
  font-size: 1.2em;
  font-weight: bold;
}

.sb-chord {
  color: $red;
  font-weight: 500;
}

.sb-menu-buttons-container {
  position: fixed;
  bottom: 10px;
  right: 10px;
  z-index: 100;
  display: flex;
  flex-direction: row;

  .sb-menu-button {
    &.sb-menu-button-cyan {
      @include button-variant($cyan, $cyan);
    }

    &.sb-menu-button-gray {
      @include button-variant($gray, $gray);
    }

    &.sb-menu-button-red {
      @include button-variant($red, $red);
    }
  
    width: 50px;
    height: 50px;
    margin-left: 5px;
  
    .sb-menu-button-icon {
      width: 50px;
      height: 50px;
      font-size: 1.3em;
    }
  }
}

.sb-menu-songtitle {
  font-size: 1.05rem;
  line-height: 1.8;
}

.sb-chosen-song {
  font-weight: bold;
  text-decoration: underline;
}

.sb-starred-song {
  font-weight: bold;
  color: $cyan;
}

.sb-star-info {
  color: $gray;
  font-weight: normal;

  &.sb-lyrics {
    font-size: 1.00rem;
    text-align: center;
  }
}

.sb-star {

  &.sb-star-plain {
    color: $lightgray;
  }

  &.sb-star-starred {
    color: $yellow;
  }

  &.sb-star-selected {
    color: $red;
  }

}
